'use client';

import Link from 'next/link';
import Image from 'next/image';
import { useState } from 'react';

export default function Navbar() {
    const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
    const [langMenuOpen, setLangMenuOpen] = useState(false);

    return (
        <nav className="navbar">
            <div className="nav-container">
                <div className="nav-logo">
                    <Link href="/">
                        <Image src="/assets/img/logo.png" alt="COVASOL Logo" width={40} height={40} />
                    </Link>
                    <Link href="/">COVASOL</Link>
                </div>
                <div className="nav-menu">
                    <Link href="/#home" className="nav-link">Trang chủ</Link>
                    <Link href="/#services" className="nav-link">Dịch vụ</Link>
                    <Link href="/products" className="nav-link">Sản phẩm</Link>
                    <Link href="/blog" className="nav-link">Blog</Link>
                    <Link href="/#about" className="nav-link">Về chúng tôi</Link>
                    <Link href="/#reviews" className="nav-link">Đánh giá</Link>
                    <Link href="/#contact" className="nav-link">Liên hệ</Link>
                </div>

                {/* Navigation Actions */}
                <div className="nav-actions">
                    <Link href="/#contact" className="nav-cta">Tư vấn miễn phí</Link>

                    {/* Language Switcher */}
                    <div className="language-switcher">
                        <div className="language-dropdown">
                            <button className="lang-btn" onClick={() => setLangMenuOpen(!langMenuOpen)}>
                                <i className="fas fa-globe"></i>
                                <span>VI</span>
                                <i className="fas fa-chevron-down"></i>
                            </button>
                            {langMenuOpen && (
                                <div className="lang-menu">
                                    <button className="lang-option active" data-lang="vi">
                                        <img src="https://flagcdn.com/w20/vn.png" alt="Vietnamese" />
                                        <span>Tiếng Việt</span>
                                    </button>
                                    <button className="lang-option" data-lang="en">
                                        <img src="https://flagcdn.com/w20/us.png" alt="English" />
                                        <span>English</span>
                                    </button>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
                <div className="nav-toggle" onClick={() => setMobileMenuOpen(!mobileMenuOpen)}>
                    <span></span>
                    <span></span>
                    <span></span>
                </div>
            </div>
        </nav>
    );
}
